import { useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../FinanceManager.module.css";
import {
  useFinanceScholarshipDepartmentFund,
  useFinanceUpdateScholarshipDepartmentFund,
} from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { useLocation } from "react-router-dom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../Utils/Functions/localTime";
import QvipleLoader from "../../../Loader/QvipleLoader";
import styleStatistics from "../StudentStatitics/StudentStatitics.module.css";

const ScholarshipFundDetail = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [disabled, setDisabled] = useState(false);
  const [financeUpdateScholarshipDepartmentFund] =
    useFinanceUpdateScholarshipDepartmentFund();

  const {
    financeScholarshipDepartmentFund,
    financeScholarshipDepartmentFundRefetch,
  } = useFinanceScholarshipDepartmentFund({
    data: {
      did: getQuery.state?.did,
    },
    skip: !getQuery.state?.did,
  });
  const onRefetch = () => {
    if (getQuery.state?.did) {
      setDisabled((pre) => !pre);
      financeUpdateScholarshipDepartmentFund({
        scholarship: {
          did: getQuery.state?.did,
        },
      })
        .then(() => {
          financeScholarshipDepartmentFundRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{getQuery.state?.dName ?? ""}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          <div
            className={style.fm_fs_container}
            style={{
              justifyContent: "flex-end",
            }}
          >
            <div className={style.fm_fs_container_inner}>
              <div
                className={style.fm_fs_refetch_container}
                onClick={onRefetch}
              >
                <img
                  src={`${assestsUserFinanceUrl}/refresh.svg`}
                  alt="refetch icon"
                />

                <div className={style.fm_fs_refetch_container_inner}>
                  <h6>{t("refetch_data")}</h6>
                  <p>
                    {t("last_updated")}
                    {" : "}
                    {dayjs(
                      financeScholarshipDepartmentFund?.last_update
                    ).format("DD MMM YYYY")}
                    {", "}

                    {localTimeFormat(
                      financeScholarshipDepartmentFund?.last_update,
                      "LT"
                    )}
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div
            className={styleStatistics.osc_container_inner}
            style={{
              justifyContent: "flex-start",
              gap: "1rem",
              flexWrap: "wrap",
            }}
          >
            {financeScholarshipDepartmentFund?.custom_classes?.map((fund) => (
              <div className={styleStatistics.osc_card}>
                <img
                  src={`${assestsUserFinanceUrl}/total-collection.svg`}
                  alt="overal icon"
                />
                <div className={styleStatistics.osc_card_inner}>
                  <p>
                    {t("batch")} {":"} {fund?.batch ?? ""}
                  </p>

                  <h6>
                    {t("collect_from_gov")} {": "}
                    {t("rs_only")} {fund?.collect_by_government ?? 0}
                  </h6>
                  <h6>
                    {t("outstanding_from_gov")} {": "}
                    {t("rs_only")} {fund?.pending_from_government ?? 0}
                  </h6>
                </div>
              </div>
            ))}
          </div>
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default ScholarshipFundDetail;
