import { useTranslation } from "react-i18next";
import style from "../FinanceManager.module.css";
import { useLocation, useParams, useSearchParams } from "react-router-dom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import { Suspense } from "react";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Allottment from "./Allottment";
import Mismatch from "./Mismatch";
import Export from "./Export";
import ScholarshipFund from "./ScholarshipFund";
import ScholarshipFundDetail from "./ScholarshipFundDetail";
const Scholarship = ({ fid, instituteId, aid }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("scholarship_management")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={
                tab.id === "allottment" && !searchParams.get("n")
                  ? true
                  : searchParams.get("n") === tab.id
                  ? true
                  : false
              }
              parentState={getQuery.state}
              url={`/q/${params.username}/member/staff/finance?type=scholarship${tab.urlKey}`}
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
      </UserMemberContentWrapper>

      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        <Suspense fallback={<QvipleLoader />}>
          {searchParams.get("d") === "fund-detail" ? (
            <ScholarshipFundDetail />
          ) : searchParams.get("n") === "mismatch" ? (
            <Mismatch fid={fid} />
          ) : searchParams.get("n") === "export" ? (
            <Export instituteId={instituteId} />
          ) : searchParams.get("n") === "fund" ? (
            <ScholarshipFund fid={fid} carryParentState={getQuery.state} />
          ) : (
            <Allottment instituteId={instituteId} aid={aid} fid={fid} />
          )}
        </Suspense>
      </UserMemberContentWrapper>
    </UserMemberContainerWrapper>
  );
};

export default Scholarship;

const tabList = [
  {
    label: "allottment",
    id: "allottment",
    urlKey: "",
  },
  {
    label: "fund",
    id: "fund",
    urlKey: "&n=fund",
  },
  {
    label: "mismatch",
    id: "mismatch",
    urlKey: "&n=mismatch",
  },
  {
    label: "export",
    id: "export",
    urlKey: "&n=export",
  },
];
