import style from "../FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useFinanceAllotScholarshipExcelHistory } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import ExportCard from "./ExportCard";
import ScholarshipExportModal from "./ScholarshipExportModal";
const Export = ({ instituteId }) => {
  const { t } = useTranslation();
  const [openFilter, setOpenFilter] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    financeAllotScholarshipExcelHistory,
    financeAllotScholarshipExcelHistoryLoading,
  } = useFinanceAllotScholarshipExcelHistory({
    data: {
      id: instituteId,
      page: page,
      limit: 10,
      search: search,
      flow: "BY_FINANCE",
    },
    skip: !instituteId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeAllotScholarshipExcelHistory?.list?.length) {
      let obj = nextPage(financeAllotScholarshipExcelHistory?.list?.length);
      setState(() => obj);
    }
  }, [financeAllotScholarshipExcelHistory?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = useCallback(() => {
    setOpenFilter((pre) => !pre);
  }, []);
  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBlock: "0.6rem",
        }}
      >
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
        <img
          src={`${assestsUserFinanceUrl}/filter.svg`}
          alt="filter icon"
          onClick={onToggle}
        />
      </div>

      {financeAllotScholarshipExcelHistory?.list?.map((excel, index) =>
        financeAllotScholarshipExcelHistory?.list?.length === index + 1 ? (
          <div key={excel?._id} ref={ref}>
            <ExportCard excel={excel} />
          </div>
        ) : (
          <ExportCard excel={excel} key={excel?._id} />
        )
      )}
      {financeAllotScholarshipExcelHistoryLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        !financeAllotScholarshipExcelHistory?.list?.length && (
          <EmptyMessage title="No export excel fuond related this search." />
        )
      ) : (
        !financeAllotScholarshipExcelHistory?.list?.length && (
          <EmptyMessage title="No export excel." />
        )
      )}
      {openFilter && (
        <ScholarshipExportModal onClose={onToggle} instituteId={instituteId} />
      )}
    </>
  );
};

export default Export;
