import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useFinanceAllotScholarshipHistory } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import style from "../FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import AllottmentListCard from "./AllottmentListCard";
const AllottmentList = ({ fid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    financeAllotScholarshipHistory,
    financeAllotScholarshipHistoryLoading,
  } = useFinanceAllotScholarshipHistory({
    data: {
      fid: fid,
      page: page,
      limit: 10,
      search: search,
      flow: "BY_FINANCE",
    },
    skip: !fid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeAllotScholarshipHistory?.list?.length) {
      let obj = nextPage(financeAllotScholarshipHistory?.list?.length);
      setState(() => obj);
    }
  }, [financeAllotScholarshipHistory?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBlock: "0.6rem",
        }}
      >
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
      </div>

      {financeAllotScholarshipHistory?.list?.map((candidate, index) =>
        financeAllotScholarshipHistory?.list?.length === index + 1 ? (
          <div key={candidate?._id} ref={ref}>
            <AllottmentListCard candidate={candidate} />
          </div>
        ) : (
          <AllottmentListCard candidate={candidate} key={candidate?._id} />
        )
      )}
      {financeAllotScholarshipHistoryLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        !financeAllotScholarshipHistory?.list?.length && (
          <EmptyMessage title="No allot scholarship students fuond related this search." />
        )
      ) : (
        !financeAllotScholarshipHistory?.list?.length && (
          <EmptyMessage title="No allot scholarship students." />
        )
      )}
    </>
  );
};

export default AllottmentList;
