import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import style from "./Scholarship.module.css";
const AllottmentListCard = ({ candidate }) => {
  const { t } = useTranslation();
  return (
    <div className={style.candidate_card_container}>
      <img
        src={
          candidate?.student?.studentProfilePhoto
            ? `${imageShowUrl}/${candidate?.student?.studentProfilePhoto}`
            : `${assestsFeedUrl}/feed-user-avatar.svg`
        }
        alt=""
      />
      <div className={style.candidate_card_container_text}>
        <div className={style.candidate_card_row}>
          <h6>{`${candidate?.student?.studentFirstName ?? ""} ${
            candidate?.student?.studentMiddleName ?? ""
          } ${candidate?.student?.studentLastName ?? ""}`}</h6>
          <h6>
            {t("rs_only")} {candidate?.fee_payment_amount ?? 0}
          </h6>
        </div>
        <div className={style.candidate_card_row}>
          <p>
            {t("gr_number")}
            {candidate?.student?.studentGRNO ?? ""} {", "}
            {`(${candidate?.student?.batches?.batchName ?? ""})`}
          </p>
          <p>{dayjs(candidate?.fee_transaction_date)?.format("DD MMM YYYY")}</p>
        </div>
        <div className={style.candidate_card_row}>
          <p>
            {t("class_candi")}{" "}
            {candidate?.student?.studentClass?.className ?? ""}{" "}
            {candidate?.student?.studentClass?.classTitle ?? ""}
          </p>
          {/* <p>
            {t("category_candi")}{" "}
            {candidate?.student?.fee_structure?.category_master
              ?.category_name ?? ""}
          </p> */}
        </div>
      </div>
    </div>
  );
};

export default AllottmentListCard;
