import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../Income/Income.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useState } from "react";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../Utils/Functions/dateUtility";
import { useFinanceAllotScholarshipExcelExport } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
const ScholarshipExportModal = ({ onClose, instituteId }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    from: "",
    to: "",
  });
  const [financeAllotScholarshipExcelExport] =
    useFinanceAllotScholarshipExcelExport();
  const onExport = () => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      financeAllotScholarshipExcelExport({
        id: instituteId,
        from: state?.from ? ymdSlashDateFormat(state.from) : "",
        to: state?.to ? ymdSlashDateFormat(state.to) : "",
        timeline: "false",
        timeline_content: "",
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onForm = (value) => {
    setState((prev) => ({
      ...prev,
      from: slashDateFormat(value),
    }));
  };

  const onTo = (value) => {
    setState((prev) => ({
      ...prev,
      to: slashDateFormat(value),
    }));
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("export_scholarship_excel")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormCalendarInput
              name="from"
              label={t("o__from")}
              placeholder={t("o__please_select_date")}
              onChange={onForm}
              value={state.from}
            />
            <FormCalendarInput
              name="to"
              label={t("o__to")}
              placeholder={t("o__please_select_date")}
              onChange={onTo}
              value={state.to}
            />

            <CreateButton label="export" onAction={onExport} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default ScholarshipExportModal;
